export const changeStopType = () => {
    const stopType = document.querySelector('.js-shipper-stop-type');
    const dateDelivery = document.querySelector('.js-delivery-date-setup');
    const datePickUp = document.querySelector('.js-pick-up-date-setup');
    const shipperDate = document.querySelector('.js-shipper-date');
    if (!stopType || !shipperDate)
        return;
    stopType.addEventListener('change', (event) => {
        const selectedValue = event.target.value;
        if (selectedValue === 'pick_up_location' && datePickUp) {
            shipperDate.value = datePickUp.value;
        }
        else if (selectedValue === 'delivery_location' && dateDelivery) {
            shipperDate.value = dateDelivery.value;
        }
    });
};
